import React from 'react';
import {
  Navigate, RouterProvider, createBrowserRouter, createHashRouter,
} from 'react-router-dom';
import Layout from './components/Layout';
import ErrorPage from './pages/ErrorPage';
import QueuePage, { loader as queuesLoader } from './pages/QueuePage';
import ConversationPage, { loader as conversationLoader } from './pages/ConversationPage';
import ConversationWrapupPage, { loader as conversationWrapupLoader } from './pages/ConversationWrapupPage';
import ConversationsPage, { loader as conversationsLoader } from './pages/ConversationsPage';

function App() {
  // const router = createBrowserRouter([
  const router = createHashRouter([
    {
      path: '/',
      element: <Layout />,
      errorElement: <ErrorPage />,
      children: [
        {
          path: 'queues',
          id: 'userQueues',
          element: <QueuePage />,
          loader: queuesLoader,
        },
        {
          path: 'conversations',
          id: 'conversations',
          element: <ConversationsPage />,
          loader: conversationsLoader,
        },
        {
          path: 'conversations/:conversationId',
          id: 'conversation',
          element: <ConversationPage />,
          loader: conversationLoader,
        },
        {
          path: 'conversations/:conversationId/wrapup',
          id: 'conversation-wrapup',
          element: <ConversationWrapupPage />,
          loader: conversationWrapupLoader,
        },
        {
          index: true,
          element: <Navigate to="/conversations" replace />,
        },
      ],
    },
  ]);

  return <RouterProvider router={router} />;
}

export default App;
