import React, { useState } from 'react';
import PropTypes from 'prop-types';
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import IconButton from '@mui/material/IconButton';
import SendIcon from '@mui/icons-material/Send';
import { getCommunicationId } from '../helpers/gCloudHelpers';

/* global platformClient */

function ConversationMessageInput(props) {
  const { conversation } = props;
  const [inputMessage, setInputMessage] = useState('');

  console.log(conversation);

  const handleInputMessageChange = (e) => {
    setInputMessage(e.target.value);
  };

  const handleSendButtonClick = async () => {
    const userId = localStorage.getItem('userId');
    const conversationsApi = new platformClient.ConversationsApi();
    const conversationId = conversation.id;
    const meParticipant = conversation.participants
      .find((participant) => participant.userId === userId);
    const communicationId = getCommunicationId(meParticipant);
    const body = {
      textBody: inputMessage,
    };
    const opts = {
      useNormalizedMessage: true,
    };
    await conversationsApi.postConversationsMessageCommunicationMessages(
      conversationId,
      communicationId,
      body,
      opts,
    );
    setInputMessage('');
  };

  return (
    <Box sx={{ width: '100%', display: 'flex', flexDirection: 'row' }}>
      <TextField
        fullWidth
        autoComplete="off"
        value={inputMessage}
        onChange={handleInputMessageChange}
      />
      <IconButton
        disabled={inputMessage.length === 0}
        onClick={handleSendButtonClick}
      >
        <SendIcon />
      </IconButton>
    </Box>
  );
}

ConversationMessageInput.propTypes = {
  conversation: PropTypes.shape({
    id: PropTypes.string.isRequired,
    participants: PropTypes.arrayOf(
      PropTypes.shape({
        userId: PropTypes.string.isRequired,
      }).isRequired,
    ),
  }).isRequired,
};

export default ConversationMessageInput;
