import React from 'react';
import PropTypes from 'prop-types';
import Box from '@mui/material/Box';
import { DateTime } from 'luxon';
import './MessageBubble.css';

function MessageBubble(props) {
  const { message } = props;
  const { normalizedMessage } = message;
  const {
    channel, direction, events = [], text,
  } = normalizedMessage;
  const { time } = channel;

  return (
    <Box
      className={`message message-${direction.toLowerCase()}`}
      sx={{ px: 2, py: 0.5, width: 'fit-content' }}
    >
      {events.length === 0
        ? (
          <>
            <span>{text}</span>
            {!text && <span className="message-error">Message has no text content</span>}
          </>
        )
        : <span>typing</span>}
      <span className="message-time">{DateTime.fromISO(time).toLocaleString(DateTime.TIME_SIMPLE)}</span>
    </Box>
  );
}

MessageBubble.propTypes = {
  message: PropTypes.shape({
    normalizedMessage: PropTypes.shape({
      channel: PropTypes.shape({
        time: PropTypes.string.isRequired,
      }).isRequired,
      direction: PropTypes.string.isRequired,
      events: PropTypes.arrayOf(
        PropTypes.shape({
          eventType: PropTypes.string.isRequired,
        }),
      ),
      text: PropTypes.string,
    }).isRequired,
  }).isRequired,
};

export default MessageBubble;
