import React, { useEffect, useState } from 'react';
import { useAsyncValue, useNavigate } from 'react-router-dom';
import Button from '@mui/material/Button';
import Box from '@mui/material/Box';
import List from '@mui/material/List';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemText from '@mui/material/ListItemText';
import ListSubheader from '@mui/material/ListSubheader';
import { conversationIsConnected, getCommunicationId, getCurrentMessage } from '../helpers/gCloudHelpers';
import { wrapupConversation } from '../services/gcloud/conversationsService';

function ConversationWrapup() {
  const loadedConversationWrapupData = useAsyncValue();
  const {
    conversation, participant, wrapupCodes,
  } = loadedConversationWrapupData;
  const [selectedWrapupCodeId, setSelectedWrapupCodeId] = useState();
  const navigate = useNavigate();

  const currentMessage = getCurrentMessage(participant);

  useEffect(() => {
    if (!currentMessage) {
      navigate('/conversations');
    }
  }, []);

  if (!currentMessage) {
    return (<Box />);
  }

  const { afterCallWorkRequired } = currentMessage;

  const canMarkDone = afterCallWorkRequired === false
    || (afterCallWorkRequired === true && selectedWrapupCodeId);

  const handleButtonDoneClick = async () => {
    const conversationId = conversation.id;
    const communicationId = getCommunicationId(participant);
    if (selectedWrapupCodeId) {
      await wrapupConversation(
        conversationId,
        participant.id,
        communicationId,
        selectedWrapupCodeId,
      );
    }
    const isConnected = conversationIsConnected(conversation);
    if (isConnected === true) {
      navigate(`/conversations/${conversationId}`);
    } else {
      navigate('/conversations');
    }
  };

  const handleListItemClick = (wrapupCodeId) => {
    setSelectedWrapupCodeId(wrapupCodeId);
  };

  return (
    <Box sx={{ width: '100%', display: 'flex', flexDirection: 'column' }}>
      <List
        subheader={(
          <ListSubheader>
            After Call Work
            {afterCallWorkRequired === false && ' - Wrapup-code is optional' }
          </ListSubheader>
        )}
        sx={{ height: '100%', overflow: 'auto' }}
      >
        {wrapupCodes.map((wrapupCode) => (
          <ListItemButton
            key={wrapupCode.id}
            selected={selectedWrapupCodeId === wrapupCode.id}
            divider
            onClick={() => handleListItemClick(wrapupCode.id)}
          >
            <ListItemText
              primary={wrapupCode.name}
              primaryTypographyProps={{ style: { textOverflow: 'ellipsis', overflow: 'hidden' } }}
            />
          </ListItemButton>
        ))}
      </List>
      <Button
        variant="contained"
        disabled={!canMarkDone}
        onClick={handleButtonDoneClick}
      >
        Done
      </Button>
    </Box>
  );
}

export default ConversationWrapup;
