import React, { useState } from 'react';
import { useSelector } from 'react-redux';
import PropTypes from 'prop-types';
import Collapse from '@mui/material/Collapse';
import List from '@mui/material/List';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemText from '@mui/material/ListItemText';
import ExpandLess from '@mui/icons-material/ExpandLess';
import ExpandMore from '@mui/icons-material/ExpandMore';
import { groupBy } from '../helpers/arrayHelpers';
import { patchUserPresences } from '../services/gcloud/presenceService';

const getPresenceLabel = (presenceDefinition) => Object.entries(
  presenceDefinition.languageLabels,
)[0][1];

const presenceDefinitionComparator = (a, b) => {
  if (a.createdDate > b.createdDate) {
    return 1;
  }
  if (a.createdDate < b.createdDate) {
    return -1;
  }
  return 0;
};

function PresenceSelector(props) {
  const { presenceDefinitions, onPresenceChanged } = props;
  const [openPresence, setOpenPresence] = useState();
  const me = useSelector((state) => state.me.user);

  const presences = groupBy(
    presenceDefinitions,
    (presenceDefinition) => presenceDefinition.systemPresence,
  );

  const changeUserStatus = async (presenceDefinition) => {
    try {
      await patchUserPresences(me.id, presenceDefinition.id);
      onPresenceChanged?.();
    } catch (err) {
      console.error(err);
      throw err;
    }
  };

  const handlePrimaryStatusClick = (presenceGroup) => {
    const [systemPresence, systemPresencePresenceDefinitions] = presenceGroup;
    if (systemPresencePresenceDefinitions.length > 1) {
      if (openPresence === systemPresence) {
        setOpenPresence();
      } else {
        setOpenPresence(systemPresence);
      }
    } else {
      changeUserStatus(systemPresencePresenceDefinitions[0]);
    }
  };

  const handleSecondaryStatusClick = (presenceDefinition) => {
    changeUserStatus(presenceDefinition);
  };

  return (
    <List>
      {Object.entries(presences)
        .map((presenceGroup) => {
          const [systemPresence, systemPresencePresenceDefinitions] = presenceGroup;
          return (
            <div key={systemPresence}>
              <ListItemButton onClick={() => handlePrimaryStatusClick(presenceGroup)}>
                <ListItemText primary={systemPresence} />
                {systemPresencePresenceDefinitions.length > 1
              && (openPresence === systemPresence ? <ExpandLess /> : <ExpandMore />)}
              </ListItemButton>
              {systemPresencePresenceDefinitions.length > 1
              && (
              <Collapse in={systemPresence === openPresence}>
                {systemPresencePresenceDefinitions
                  .filter((presenceDefinition) => presenceDefinition.primary === false)
                  .sort(presenceDefinitionComparator)
                  .map((presenceDefinition) => {
                    const presenceLabel = getPresenceLabel(presenceDefinition);
                    return (
                      <List
                        key={presenceDefinition.id}
                        disablePadding
                      >
                        <ListItemButton
                          sx={{ pl: 4 }}
                          onClick={() => handleSecondaryStatusClick(presenceDefinition)}
                        >
                          <ListItemText primary={presenceLabel} />
                        </ListItemButton>
                      </List>
                    );
                  })}
              </Collapse>
              )}
            </div>
          );
        })}
    </List>
  );
}

PresenceSelector.propTypes = {
  presenceDefinitions: PropTypes.arrayOf(
    PropTypes.shape({
      systemPresence: PropTypes.string.isRequired,
    }),
  ).isRequired,
  onPresenceChanged: PropTypes.func,
};

PresenceSelector.defaultProps = {
  onPresenceChanged: null,
};

export default PresenceSelector;
