const checkAllPermissions = (permissions, me) => {
  const assignedPermissions = me?.authorization?.permissions ?? [];
  return permissions
    .every(
      (permission) => assignedPermissions
        .filter((assignedPermission) => assignedPermission.indexOf(permission) !== -1),
    );
};

const checkAnyPermissions = (permissions, me) => {
  const assignedPermissions = me?.authorization?.permissions ?? [];
  return permissions
    .some(
      (permission) => assignedPermissions
        .filter((assignedPermission) => assignedPermission.indexOf(permission) !== -1),
    );
};

const getCurrentMessage = (participant) => participant.messages?.find((message) => message.state === 'connected' || message.afterCallWork?.state === 'pending');

const getCommunicationId = (participant) => {
  const currentMessage = getCurrentMessage(participant);
  return currentMessage?.id;
};

const getConversationMessagesIds = (conversation) => conversation.participants.reduce(
  (accumulator, currentValue) => {
    const messages = currentValue.messages
      .flatMap((message) => message.messages ?? [])
      .map((message) => message.messageId);
    return accumulator.concat(messages);
  },
  [],
);

const getConversationAgentParticipants = (conversation) => conversation.participants.filter((participant) => participant.purpose === 'agent');

const getConnectedAgentParticipant = (conversation) => {
  const agentParticipants = getConversationAgentParticipants(conversation);
  return agentParticipants.find(
    (participant) => participant.messages?.find((message) => message.state === 'connected')
    || participant.chats?.find((chat) => chat.state === 'connected'),
  );
};

const conversationIsConnected = (conversation) => getConnectedAgentParticipant(conversation)
  !== undefined;

const messageConversationIsConnected = (conversation) => conversation?.participants.find((participant) => participant.state === 'connected');

const messagesComparator = (a, b) => {
  if (a.timestamp < b.timestamp) {
    return -1;
  }
  if (a.timestamp > b.timestamp) {
    return 1;
  }
  return 0;
};

export {
  checkAllPermissions,
  checkAnyPermissions,
  getCommunicationId,
  getConversationMessagesIds,
  getCurrentMessage,
  conversationIsConnected,
  getConnectedAgentParticipant,
  messageConversationIsConnected,
  messagesComparator,
};
