import React, { useEffect, useState } from 'react';
import { NavLink } from 'react-router-dom';
import PropTypes from 'prop-types';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import ReactTimeAgo from 'react-time-ago';
import MessageIcon from '@mui/icons-material/Message';
import { getRoutingQueue } from '../services/gcloud/routingService';

/* global platformClient */

/* notification api doesn´t contain startTime property */
const getConversationStartTime = (conversation) => {
  if (conversation.startTime) {
    return conversation.startTime;
  }
  return conversation.participants.map((participant) => participant.connectedTime).sort()[0];
};

function ConversationListItem(props) {
  const { conversation } = props;
  const [queue, setQueue] = useState();
  const [externalContact, setExternalContact] = useState();
  const customerParticipant = conversation?.participants?.find((participant) => participant.purpose === 'customer');
  const conversationStartTime = getConversationStartTime(conversation);

  useEffect(() => {
    const loadQueue = async (queueId) => {
      const loadedQueue = await getRoutingQueue(queueId);
      setQueue(loadedQueue);
    };

    if (customerParticipant) {
      loadQueue(customerParticipant.queueId);
    }
  }, [conversation]);

  useEffect(() => {
    const loadExternalContact = async (contactId) => {
      const externalContactsApi = new platformClient.ExternalContactsApi();
      const opts = {};
      const loadedExternalContact = await externalContactsApi
        .getExternalcontactsContact(contactId, opts);
      setExternalContact(loadedExternalContact);
    };

    if (customerParticipant) {
      loadExternalContact(customerParticipant.externalContactId);
    }
  }, [conversation]);

  return (
    <ListItem disablePadding component={NavLink} to={`/conversations/${conversation.id}`}>
      <ListItemButton divider>
        <ListItemIcon>
          <MessageIcon />
        </ListItemIcon>
        <ListItemText
          primary={externalContact?.name ?? 'Customer'}
          primaryTypographyProps={{ style: { textOverflow: 'ellipsis', overflow: 'hidden' } }}
          secondary={queue?.name}
          secondaryTypographyProps={{ style: { textOverflow: 'ellipsis', overflow: 'hidden' } }}
        />
        <ReactTimeAgo date={new Date(conversationStartTime)} />
      </ListItemButton>
    </ListItem>
  );
}

ConversationListItem.propTypes = {
  conversation: PropTypes.shape({
    id: PropTypes.string.isRequired,
    participants: PropTypes.arrayOf(PropTypes.shape({
      purpose: PropTypes.string.isRequired,
    })).isRequired,
  }).isRequired,
};

export default ConversationListItem;
