import React, { useEffect, useState } from 'react';
import { Outlet } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import Box from '@mui/material/Box';
import useWebSocket, { ReadyState } from 'react-use-websocket';
import TopAppBar from './TopAppBar';
import { meActions } from '../store/me';
import { notificationsActions } from '../store/notifications';
import './Layout.css';
import { getUsersMe } from '../services/gcloud/usersService';
import { patchUserPresences } from '../services/gcloud/presenceService';

/* global platformClient */

const subscribeToTopics = async (userId, channelId) => {
  console.log('subscribeToTopics()');
  const notificationsApi = new platformClient.NotificationsApi();
  const body = [
    `v2.users.${userId}.conversations`,
    `v2.users.${userId}.presence`,
    `v2.users.${userId}.conversations.inbound.typing.event`,
  ];
  const opts = {};
  try {
    const data = await notificationsApi.putNotificationsChannelSubscriptions(channelId, body, opts);
    console.log(`putNotificationsChannelSubscriptions success! data: ${JSON.stringify(data, null, 2)}`);
  } catch (err) {
    console.log('There was a failure calling putNotificationsChannelSubscriptions');
    console.error(err);
  }
};

function Layout() {
  const dispatch = useDispatch();
  const me = useSelector((state) => state.me.user);
  const [socketUrl, setSocketUrl] = useState();
  const [channelId, setChannelId] = useState();
  const [backgroundColor, setBackgroundColor] = useState();
  const { lastMessage, readyState } = useWebSocket(socketUrl, {}, socketUrl !== undefined);

  useEffect(() => {
    const loadUsersMe = async () => {
      try {
        const data = await getUsersMe();
        dispatch(meActions.load(data));
      } catch (err) {
        console.error(err);
      }
    };

    loadUsersMe();
  }, []);

  useEffect(() => {
    if (me) {
      const createNewNotificationsChannel = async () => {
        console.log('createNewNotificationsChannel()');
        const notificationsApi = new platformClient.NotificationsApi();
        try {
          const data = await notificationsApi.postNotificationsChannels();
          console.log(`postNotificationsChannels success! data: ${JSON.stringify(data, null, 2)}`);
          setSocketUrl(data.connectUri);
          setChannelId(data.id);
        } catch (err) {
          console.log('There was a failure calling postNotificationsChannels');
          console.error(err);
        }
      };
      createNewNotificationsChannel();
    }
  }, [me]);

  useEffect(() => {
    const subscribe = async () => {
      await subscribeToTopics(me.id, channelId);
      if (me?.presence?.presenceDefinition.systemPresence === 'Offline') {
        const availablePresenceDefinition = me.presenceDefinitions.find((presenceDefinition) => presenceDefinition.systemPresence === 'Available');
        patchUserPresences(me.id, availablePresenceDefinition.id);
      }
    };
    if (channelId && readyState === ReadyState.OPEN) {
      subscribe();
    }
  }, [channelId, readyState]);

  useEffect(() => {
    if (lastMessage) {
      const data = JSON.parse(lastMessage.data);
      const topic = data.topicName;
      const { eventBody } = data;
      console.log(eventBody);
      if (topic.indexOf('typing') !== -1) {
        dispatch(notificationsActions.handleUserConversationsTypingEvent(eventBody));
      } else if (topic.indexOf('conversations') !== -1) {
        dispatch(notificationsActions.handleUserConversationsEvent(eventBody));
      } else if (topic.indexOf('presence') !== -1) {
        dispatch(meActions.presenceChanged(eventBody));
      }
    }
  }, [lastMessage]);

  useEffect(() => {
    console.log(readyState);
    switch (readyState) {
      case ReadyState.OPEN:
        setBackgroundColor('#ff4f1f');
        break;
      default:
        setBackgroundColor('transparent');
        break;
    }
  }, [readyState]);

  return (
    <Box className="App">
      <Box sx={{ height: '5px', backgroundColor }} />
      <TopAppBar title="" />
      <Outlet />
    </Box>
  );
}

export default Layout;
