import React, { Suspense } from 'react';
import { useRouteLoaderData, defer, Await } from 'react-router-dom';
import CircularProgress from '@mui/material/CircularProgress';
import ErrorPage from './ErrorPage';
import Conversations from '../components/Conversations';
import { getConversations } from '../services/gcloud/conversationsService';

function ConversationsPage() {
  const { conversations } = useRouteLoaderData('conversations');
  return (
    <Suspense fallback={<CircularProgress />}>
      <Await resolve={conversations} errorElement={<ErrorPage />}>
        <Conversations />
      </Await>
    </Suspense>
  );
}

export async function loader() {
  return defer({
    conversations: getConversations(),
  });
}

export default ConversationsPage;
