/* global platformClient */

const patchUserPresences = async (userId, presenceDefinitionId) => {
  const presenceApi = new platformClient.PresenceApi();
  const body = {
    presenceDefinition: {
      id: presenceDefinitionId,
    },
  };
  try {
    const data = await presenceApi.patchUserPresencesPurecloud(
      userId,
      body,
    );
    console.log(`patchUserPresencesPurecloud success! data: ${JSON.stringify(data, null, 2)}`);
    return;
  } catch (err) {
    console.log('There was a failure calling patchUserPresencesPurecloud');
    console.error(err);
    throw err;
  }
};

export { patchUserPresences };
