import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import PropTypes from 'prop-types';
import IconButton from '@mui/material/IconButton';
import Toolbar from '@mui/material/Toolbar';
import Tooltip from '@mui/material/Tooltip';
import CommentsDisabledIcon from '@mui/icons-material/CommentsDisabled';
import AssignmentTurnedInIcon from '@mui/icons-material/AssignmentTurnedIn';
import { conversationIsConnected } from '../helpers/gCloudHelpers';

function ConversationToolBar(props) {
  const { conversation, onDisconnect, onWrapup } = props;
  const notifiedConversation = useSelector((state) => state.notifications.conversation);
  const [isConnected, setIsConnected] = useState(conversationIsConnected(conversation));

  useEffect(() => {
    if (notifiedConversation?.id === conversation.id) {
      setIsConnected(conversationIsConnected(notifiedConversation));
    }
  }, [notifiedConversation]);

  const handleButtonDisconnectClick = () => {
    onDisconnect?.();
  };

  const handleButtonWrapupClick = () => {
    onWrapup?.();
  };

  return (
    <Toolbar>
      <Tooltip title="Disconnect">
        <IconButton disabled={!isConnected} onClick={handleButtonDisconnectClick}>
          <CommentsDisabledIcon />
        </IconButton>
      </Tooltip>
      <Tooltip title="Wrapup">
        <IconButton onClick={handleButtonWrapupClick}>
          <AssignmentTurnedInIcon />
        </IconButton>
      </Tooltip>
    </Toolbar>
  );
}

ConversationToolBar.propTypes = {
  conversation: PropTypes.shape({
    id: PropTypes.string.isRequired,
  }).isRequired,
  onDisconnect: PropTypes.func.isRequired,
  onWrapup: PropTypes.func.isRequired,
};

export default ConversationToolBar;
