/* global platformClient */

const disconnectConversation = async (conversationId) => {
  try {
    const conversationsApi = new platformClient.ConversationsApi();
    const opts = {
      state: 'DISCONNECTED',
    };
    await conversationsApi.patchConversationsMessage(
      conversationId,
      opts,
    );
  } catch (err) {
    console.error(err);
    throw err;
  }
};

const getConversation = async (conversationId) => {
  try {
    const conversationsApi = new platformClient.ConversationsApi();
    const conversation = await conversationsApi.getConversation(conversationId);
    return conversation;
  } catch (err) {
    console.error(err);
    throw err;
  }
};

const getChatConversationMessages = async (conversationId) => {
  try {
    const conversationsApi = new platformClient.ConversationsApi();
    const opts = { };
    const messages = await conversationsApi.getConversationsChatMessages(
      conversationId,
      opts,
    );
    return messages.entities;
  } catch (err) {
    console.error(err);
    throw err;
  }
};

const getConversationMessages = async (conversationId, messagesIds) => {
  if (messagesIds.length === 0) {
    return [];
  }
  try {
    const conversationsApi = new platformClient.ConversationsApi();
    const opts = {
      body: messagesIds,
      useNormalizedMessage: true,
    };
    const messages = await conversationsApi.postConversationsMessageMessagesBulk(
      conversationId,
      opts,
    );
    return messages.entities;
  } catch (err) {
    console.error(err);
    throw err;
  }
};

const getConversations = async () => {
  try {
    const conversationsApi = new platformClient.ConversationsApi();
    const opts = {};
    const response = await conversationsApi.getConversations(opts);
    return response.entities;
  } catch (err) {
    console.error(err);
    throw err;
  }
};

const wrapupConversation = async (conversationId, participantId, communicationId, wrapupCodeId) => {
  try {
    const conversationsApi = new platformClient.ConversationsApi();
    const opts = {
      body: {
        code: wrapupCodeId,
      },
    };
    await conversationsApi.postConversationsMessageParticipantCommunicationWrapup(
      conversationId,
      participantId,
      communicationId,
      opts,
    );
  } catch (err) {
    console.error(err);
    throw err;
  }
};

export {
  disconnectConversation,
  getChatConversationMessages,
  getConversation,
  getConversationMessages,
  getConversations,
  wrapupConversation,
};
