import React, { useEffect, useRef, useState } from 'react';
import { useSelector } from 'react-redux';
import PropTypes from 'prop-types';
import Divider from '@mui/material/Divider';
import Box from '@mui/material/Box';
import { DateTime } from 'luxon';
import MessageBubble from './MessageBubble';
import { groupBy } from '../helpers/arrayHelpers';
import { getConversationMessages } from '../services/gcloud/conversationsService';
import { getConversationMessagesIds, messagesComparator } from '../helpers/gCloudHelpers';

const mergeMessages = async (messages, notifiedConversation, conversationId) => {
  let mergedMessages = messages;
  const messagesIds = messages.map((message) => message.id);
  const notifiedMessagesIds = getConversationMessagesIds(notifiedConversation);
  const newMessagesIds = notifiedMessagesIds.filter((id) => !messagesIds.includes(id));
  if (newMessagesIds.length > 0) {
    const newMessages = await getConversationMessages(conversationId, newMessagesIds);
    mergedMessages = mergedMessages.concat(newMessages);
    mergedMessages.sort(messagesComparator);
  }
  return mergedMessages;
};

function ConversationMessages(props) {
  const { conversation, messages: loadedMessages } = props;
  const [messages, setMessages] = useState(loadedMessages);
  const [typingMessage, setTypingMessage] = useState();
  const notifiedConversation = useSelector((state) => state.notifications.conversation);
  const notifiedTypingEvent = useSelector((state) => state.notifications.typingEvent);
  const messagesEndRef = useRef(null);

  const groupedMessages = groupBy(messages, (message) => DateTime.fromISO(message.timestamp).toFormat('dd LLL, yyyy'));

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  useEffect(() => {
    const merge = async () => {
      if (notifiedConversation && notifiedConversation.id === conversation.id) {
        const mergedMessages = await mergeMessages(
          loadedMessages,
          notifiedConversation,
          conversation.id,
        );
        setMessages(mergedMessages);
      }
    };
    merge();
  }, [loadedMessages, notifiedConversation]);

  useEffect(() => {
    if (notifiedTypingEvent?.conversationId === conversation.id) {
      /* TODO: typing */
      // setTypingMessage(notifiedTypingEvent);
    }
  }, [notifiedTypingEvent]);

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  return (
    <Box sx={{ flex: 1, overflowY: 'auto' }}>
      {Object.keys(groupedMessages).map((date) => (
        <Box key={date}>
          <Divider>{date}</Divider>
          <Box sx={{ display: 'flex', flexDirection: 'column' }}>
            {
              groupedMessages[date].map(
                (message) => (<MessageBubble key={message.id} message={message} />),
              )
            }
            {typingMessage && <MessageBubble message={typingMessage} />}
          </Box>
        </Box>
      ))}
      <div ref={messagesEndRef} />
    </Box>
  );
}

ConversationMessages.propTypes = {
  conversation: PropTypes.shape({
    id: PropTypes.string.isRequired,
  }).isRequired,
  messages: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.string.isRequired,
    }).isRequired,
  ).isRequired,
};

export default ConversationMessages;
