/* global platformClient */

const getRoutingQueue = async (queueId) => {
  const routingApi = new platformClient.RoutingApi();
  try {
    const data = await routingApi.getRoutingQueue(queueId);
    return data;
  } catch (err) {
    console.log('There was a failure calling getRoutingQueue');
    console.error(err);
    throw err;
  }
};

const getUserQueues = async (userId) => {
  const routingApi = new platformClient.RoutingApi();
  const opts = {
    pageNumber: 1,
    pageSize: 100,
    sortOrder: 'asc',
  };
  try {
    const data = await routingApi.getUserQueues(userId, opts);
    console.log(`getUserQueues success! data: ${JSON.stringify(data, null, 2)}`);
    return data.entities;
  } catch (err) {
    console.log('There was a failure calling getUserQueues');
    console.error(err);
    throw err;
  }
};

export { getRoutingQueue, getUserQueues };
