import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import IconButton from '@mui/material/IconButton';
import Avatar from '@mui/material/Avatar';
import Divider from '@mui/material/Divider';
import MenuItem from '@mui/material/MenuItem';
import Menu from '@mui/material/Menu';
import Switch from '@mui/material/Switch';
import FormControlLabel from '@mui/material/FormControlLabel';
import Badge from '@mui/material/Badge';
import PresenceSelector from './PresenceSelector';
import { patchUserPresences } from '../services/gcloud/presenceService';
import { unassignStation } from '../services/gcloud/stationsService';

/* global logout */

const getAvatarImage = (user) => {
  if (user && user.images?.length > 0) {
    return user.images[0].imageUri;
  }
  return null;
};

const defaultPresencesOrder = ['Available', 'Busy', 'Away', 'Break', 'Meal', 'Meeting', 'Training'];

const presenceDefinitionComparator = (a, b) => {
  const indexA = defaultPresencesOrder.indexOf(a.systemPresence);
  const indexB = defaultPresencesOrder.indexOf(b.systemPresence);
  if (indexA > indexB) {
    return 1;
  }
  if (indexA < indexB) {
    return -1;
  }
  return 0;
};

function TopAppBarAvatar() {
  const me = useSelector((state) => state.me.user);
  const presence = useSelector((state) => state.me.presence);
  const [onQueue, setOnQueue] = useState(false);
  const [currentSystemPresence, setCurrentSystemPresence] = useState();
  const [menuAnchorEl, setMenuAnchorEl] = useState(null);

  const presenceDefinitions = me?.presenceDefinitions.toSorted(presenceDefinitionComparator) || [];
  const avatarSrc = getAvatarImage(me);

  useEffect(() => {
    setCurrentSystemPresence(presence?.presenceDefinition?.systemPresence);
    setOnQueue(presence?.presenceDefinition?.systemPresence === 'On Queue');
  }, [presence]);

  const handleLogout = async () => {
    /*
    const station = me.station?.effectiveStation;
    if (station) {
      await unassignStation(station.id);
    }
    */
    try {
      const offlinePresenceDefinition = presenceDefinitions.find((presenceDefinition) => presenceDefinition.systemPresence === 'Offline');
      await patchUserPresences(me.id, offlinePresenceDefinition.id);
    } catch (err) {
      // do nothing
    }
    logout();
  };

  const handleMenu = (event) => {
    setMenuAnchorEl(event.currentTarget);
  };

  const handleMenuClose = () => {
    setMenuAnchorEl(null);
  };

  const handleOnQueueChange = async (event) => {
    try {
      if (event.currentTarget.checked) {
        localStorage.setItem('lastPresenceDefinitionId', presence?.presenceDefinition.id);
        const onQueuePresenceDefinition = presenceDefinitions.find((presenceDefinition) => presenceDefinition.systemPresence === 'On Queue');
        await patchUserPresences(me.id, onQueuePresenceDefinition.id);
      } else {
        const lastPresenceId = localStorage.getItem('lastPresenceDefinitionId');
        const lastPresenceDefinition = presenceDefinitions.find((presenceDefinition) => presenceDefinition.id === lastPresenceId) || presenceDefinitions.find((presenceDefinition) => presenceDefinition.systemPresence === 'Idle');
        await patchUserPresences(me.id, lastPresenceDefinition.id);
      }
    } catch (err) {
      console.error(err);
      throw err;
    }
    setMenuAnchorEl(null);
  };

  const handlePresenceChanged = () => {
    setMenuAnchorEl(null);
  };

  const presenceColor = currentSystemPresence?.toLowerCase();

  console.log(presenceColor);

  return (
    <>
      <IconButton
        size="large"
        color="inherit"
        onClick={handleMenu}
      >
        <Badge
          overlap="circular"
          anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
          variant="dot"
          color={presenceColor ?? 'default'}
        >
          <Avatar src={avatarSrc} />
        </Badge>
      </IconButton>
      <Menu
        anchorEl={menuAnchorEl}
        anchorOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}
        keepMounted
        transformOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}
        open={Boolean(menuAnchorEl)}
        onClose={handleMenuClose}
      >
        <FormControlLabel
          sx={{ ml: 1, mb: 1 }}
          control={<Switch checked={onQueue} onChange={handleOnQueueChange} />}
          label={onQueue ? 'On Queue' : 'Off Queue'}
        />
        <Divider />
        <PresenceSelector
          presenceDefinitions={presenceDefinitions.filter((presenceDefinition) => presenceDefinition.systemPresence !== 'Idle' && presenceDefinition.systemPresence !== 'Offline' && presenceDefinition.systemPresence !== 'On Queue')}
          onPresenceChanged={handlePresenceChanged}
        />
        <Divider />
        <MenuItem onClick={handleLogout}>Logout</MenuItem>
      </Menu>
    </>
  );
}

export default TopAppBarAvatar;
