import React from 'react';
import PropTypes from 'prop-types';
import Accordion from '@mui/material/Accordion';
import AccordionDetails from '@mui/material/AccordionDetails';
import AccordionSummary from '@mui/material/AccordionSummary';
import Typography from '@mui/material/Typography';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';

const getCustomerParticipantData = (conversation) => {
  const customerParticipant = conversation.participants.find((participant) => participant.purpose === 'customer');
  return customerParticipant?.attributes ?? [];
};

function ConversationData(props) {
  const { conversation } = props;
  const customerParticipantData = getCustomerParticipantData(conversation);
  if (customerParticipantData.length === 0) {
    return (<div />);
  }
  return (
    <Accordion>
      <AccordionSummary
        expandIcon={<ExpandMoreIcon />}
      >
        <Typography>Conversation Data</Typography>
      </AccordionSummary>
      <AccordionDetails>
        <Table size="small">
          <TableHead>
            <TableRow>
              <TableCell>Key</TableCell>
              <TableCell>Value</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {
                Object.entries(customerParticipantData).map((entry) => (
                  <TableRow key={entry[0]}>
                    <TableCell>{entry[0]}</TableCell>
                    <TableCell>{entry[1]}</TableCell>
                  </TableRow>
                ))
            }
          </TableBody>
        </Table>
      </AccordionDetails>
    </Accordion>
  );
}

ConversationData.propTypes = {
  conversation: PropTypes.shape({

  }).isRequired,
};

export default ConversationData;
