/* global platformClient */

const getUsersMe = async () => {
  const usersApi = new platformClient.UsersApi();
  const opts = {
    expand: ['presence', 'station', 'authorization', 'presencedefinitions'],
  };
  try {
    const data = await usersApi.getUsersMe(opts);
    console.log(`getUsersMe success! data: ${JSON.stringify(data, null, 2)}`);
    return data;
  } catch (err) {
    console.log('There was a failure calling getUsersMe');
    console.error(err);
    throw err;
  }
};

const patchUserQueues = async (userId, userQueues) => {
  const usersApi = new platformClient.UsersApi();
  const body = userQueues;
  const opts = {};
  try {
    const data = await usersApi.patchUserQueues(userId, body, opts);
    console.log(`patchUserQueues success! data: ${JSON.stringify(data, null, 2)}`);
    return data.entities;
  } catch (err) {
    console.log('There was a failure calling patchUserQueues');
    console.error(err);
    throw err;
  }
};

export { getUsersMe, patchUserQueues };
