import React, { useEffect, useState, Suspense } from 'react';
import {
  defer, useNavigate, useRouteLoaderData, Await,
} from 'react-router-dom';
import { useSelector } from 'react-redux';
import Alert from '@mui/material/Alert';
import CircularProgress from '@mui/material/CircularProgress';
import Collapse from '@mui/material/Collapse';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import ListSubheader from '@mui/material/ListSubheader';
import Switch from '@mui/material/Switch';
import BottomAppBar from '../components/BottomAppBar';
import { getUserQueues } from '../services/gcloud/routingService';
import { patchUserQueues } from '../services/gcloud/usersService';
import ErrorPage from './ErrorPage';
import { checkAllPermissions } from '../helpers/gCloudHelpers';

function QueuePage() {
  const { userQueuesData } = useRouteLoaderData('userQueues');
  const me = useSelector((state) => state.me.user);
  const [checked, setChecked] = useState([]);
  const [userQueues, setUserQueues] = useState([]);
  const [alertOpened, setAlertOpened] = useState(false);
  const navigate = useNavigate();

  useEffect(() => {
    const loadUserQueues = async () => {
      try {
        const loadedUserQueues = await getUserQueues(me.id);
        setUserQueues(loadedUserQueues);
        setChecked(
          loadedUserQueues.filter((queue) => queue.joined).map((queue) => queue.id),
        );
      } catch (err) {
        console.error(err);
      }
    };
    if (me) {
      loadUserQueues();
    }
  }, [me]);

  const handleNext = async () => {
    if (checkAllPermissions(['routing:queue:join', 'routing:queue:view'], me) === true) {
      const updatedUserQueues = userQueues.map(
        (queue) => ({
          id: queue.id,
          joined: checked.indexOf(queue.id) !== -1,
        }),
      );
      await patchUserQueues(me.id, updatedUserQueues);
      navigate('/conversations');
    } else {
      setAlertOpened(true);
    }
  };

  const handleToggle = (value) => () => {
    const currentIndex = checked.indexOf(value);
    const newChecked = [...checked];
    if (currentIndex === -1) {
      newChecked.push(value);
    } else {
      newChecked.splice(currentIndex, 1);
    }
    setChecked(newChecked);
  };

  return (
    <Suspense fallback={<CircularProgress />}>
      <Await resolve={userQueuesData} errorElement={<ErrorPage />}>
        <List
          subheader={<ListSubheader>Which queues are you working today?</ListSubheader>}
          sx={{ height: '100%', overflow: 'auto' }}
        >
          {
            userQueues.map((queue) => (
              <ListItem key={queue.id}>
                <ListItemText
                  primary={queue.name}
                  primaryTypographyProps={{ style: { textOverflow: 'ellipsis', overflow: 'hidden' } }}
                />
                <Switch
                  edge="end"
                  onChange={handleToggle(queue.id)}
                  checked={checked.indexOf(queue.id) !== -1}
                />
              </ListItem>
            ))
          }
        </List>
        <Collapse in={alertOpened}>
          <Alert severity="error">Missing required permissions to execute action</Alert>
        </Collapse>
        <BottomAppBar next={handleNext} />
      </Await>
    </Suspense>
  );
}

export async function loader() {
  const userId = localStorage.getItem('userId');
  return defer({
    userQueuesData: getUserQueues(userId),
  });
}

export default QueuePage;
