import { createSlice } from '@reduxjs/toolkit';

const defaultMeState = {
  user: null,
  presence: null,
};

/* eslint-disable no-param-reassign */
const meSlice = createSlice({
  name: 'me',
  initialState: defaultMeState,
  reducers: {
    load(state, action) {
      state.user = action.payload;
      state.presence = action.payload.presence;
      localStorage.setItem('userId', action.payload.id);
      return state;
    },
    presenceChanged(state, action) {
      state.presence = action.payload;
      if (state.presence.presenceDefinition.systemPresence === 'ON_QUEUE') {
        state.presence.presenceDefinition.systemPresence = 'On Queue';
      }
      return state;
    },
  },
});

export const meActions = meSlice.actions;

export default meSlice.reducer;
