import React, { Suspense } from 'react';
import { useRouteLoaderData, defer, Await } from 'react-router-dom';
import CircularProgress from '@mui/material/CircularProgress';
import Box from '@mui/material/Box';
import ErrorPage from './ErrorPage';
import ConversationWrapup from '../components/ConversationWrapup';
import { getConversation } from '../services/gcloud/conversationsService';

/* global platformClient */

const getConversationWrapupData = async (conversationId, userId) => {
  try {
    const conversationsApi = new platformClient.ConversationsApi();
    const conversation = await getConversation(conversationId);
    const participant = conversation.participants
      .find((conversationParticipant) => conversationParticipant.userId === userId);
    const wrapupCodes = await conversationsApi.getConversationsMessageParticipantWrapupcodes(
      conversationId,
      participant.id,
    );
    return {
      conversation,
      participant,
      wrapupCodes,
    };
  } catch (err) {
    console.error(err);
    throw err;
  }
};

function ConversationWrapupPage() {
  const { conversationWrapupData } = useRouteLoaderData('conversation-wrapup');
  return (
    <Box sx={{
      m: 1, height: '100%', display: 'flex', flexDirection: 'column',
    }}
    >
      <Suspense fallback={<CircularProgress />}>
        <Await resolve={conversationWrapupData} errorElement={<ErrorPage />}>
          <ConversationWrapup />
        </Await>
      </Suspense>
    </Box>
  );
}

export async function loader({ params }) {
  const { conversationId } = params;
  const userId = localStorage.getItem('userId');
  return defer({
    conversationWrapupData: getConversationWrapupData(conversationId, userId),
  });
}

export default ConversationWrapupPage;
