import React from 'react';
import { useAsyncValue, useNavigate } from 'react-router-dom';
import ConversationData from './ConversationData';
import ConversationMessages from './ConversationMessages';
import ConversationToolBar from './ConversationToolbar';
import { disconnectConversation } from '../services/gcloud/conversationsService';
import ConversationMessageInput from './ConversationMessageInput';

function Conversation() {
  const { conversation, messages } = useAsyncValue();
  const navigate = useNavigate();

  const handleDisconnect = async () => {
    await disconnectConversation(conversation.id);
    navigate('wrapup');
  };

  const handleWrapup = () => {
    navigate('wrapup');
  };

  return (
    <>
      <ConversationToolBar
        conversation={conversation}
        onDisconnect={handleDisconnect}
        onWrapup={handleWrapup}
      />
      <ConversationData
        conversation={conversation}
      />
      <ConversationMessages
        conversation={conversation}
        messages={messages}
      />
      <ConversationMessageInput
        conversation={conversation}
      />
    </>
  );
}

export default Conversation;
